.list
    .list_wrapper
        width: 90vw
        height: 90vh
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        background-color: rgba(20, 20, 20, .7)
        padding: 25px 25px
        border-radius: 5px
        overflow-y: auto
        .award
            color: yellow
            font-size: 36px
            margin-bottom: 20px
        .winner_list
            margin-bottom: 45px
            .winner
                width: 30%
                color: white
                font-size: 36px
                margin: 0 3% 20px 0
                display: inline-block
