$header-lg-height: vw(70)
$header-mobile-height: mvw(100)

header
    width: 100vw
    padding: 20px 50px
    display: flex
    align-items: center
    justify-content: space-between

.nav
    background: #bf0e29
    height: $header-lg-height
    line-height: $header-lg-height
    width: 100%
    // position: fixed
    position: relative
    left: 0
    top: 0
    +rwd($pad)
        height: $header-mobile-height
        line-height: $header-mobile-height
    a 
        cursor: pointer
    p
        display: inline-block
        margin-right: 10px  
    & .l-container
        height: 100%
    &-bd 
        height: 100%
        padding: 0 vw(28)
        display: flex
        align-items: center
        justify-content: space-between
        +rwd($desktop)
            padding: 0 mvw(23)
    &-logo 
        display: inline-block
        width: vw(213)
        line-height: 0
        vertical-align: middle
        +rwd($desktop)
            width: mvw(360)
        img
            width: 100%
            height: auto
    &-btn
        display: none
        +rwd($pad)
            width: mvw(60)
            height: mvw(60)
            cursor: pointer
            background: #fff
            border-radius: mvw(6)
            position: relative
            display: inline-flex
            align-items: center
            flex-flow: column wrap
            justify-content: space-between
            padding: mvw(15) mvw(15)
            order: 3
            >.one,
            >.two,
            >.three 
                width: 100%
                height: 2px
                background: #bf0e29
                backface-visibility: hidden
                transition-duration: 0.3s
                border-radius: mvw(3)
    &-menu
        li 
            display: inline-block
            line-height: 0
            vertical-align: middle
            margin-right: vw(30)
            span
                color: #fff
                font-weight: bold
                font-size: 16px
                line-height: 1
        +rwd($pad)
            width: mvw(375)
            background: #aaa
            height: 100vh
            right: -100%
            top: mvw(100)
            position: absolute
            transition: all 0.2s ease-out
            ul 
                margin: 0
                padding-top: mvw(20)
                padding-left: mvw(22)
                line-height: mvw(75)
                li 
                    width: 100%
                    position: relative     
    &--active 
        +rwd($pad)
            .nav-menu 
                right: 0
                position: absolute
                top: mvw(100)
                overflow: auto
            .nav-btn 
                >.one 
                    position: absolute
                    top: 50%
                    left: 0
                    transform: (rotate(45deg) translateY(-50%))      
                >.two 
                    opacity: 0
                >.three 
                    position: absolute
                    top: 50%
                    left: 0
                    transform: (rotate(-45deg) translateY(-50%))