.index
    height: 100vh
    .lottie_block
        width: 50vw
        height: 50vw
        // border: 1px solid #EEE
        position: absolute
        bottom: 0%
        left: 50%
        transform: translateX(-50%)
        &.fadeOut
            animation-name: fadeout-effect
            animation-duration: .6s
            animation-fill-mode: forwards
        .shadow_button
            position: absolute
            z-index: 10
            top: 75%
            left: 36%
            width: 15%
            padding-top: 15%
            cursor: pointer
            // border: 1px solid black
    .back_btn
        font-size: 28px
        font-weight: bold
        padding: 10px 20px
        background-color: transparent
        border: 1px solid white
        color: white
        border-radius: 10px
        display: inline-block
        position: absolute
        bottom: 60px
        left: 60px
        cursor: pointer
    .result
        width: 80vw
        max-height: 80vh
        // border: 1px solid #EEE
        position: absolute
        top: 10%
        left: 50%
        transform: translateX(-50%)
        .result_bg
            width: 100%
            max-height: 80vh
            overflow-y: auto
            display: inline-block
            background-color: rgba(20, 20, 20, .7)
            padding: 25px 25px
            border-radius: 5px
            // -webkit-filter: blur(15px)
            // filter: blur(15px)
            // position: absolute
            // top: 0
            // left: 0
        .award
            opacity: 0
            animation-duration: .6s
            animation-fill-mode: forwards
            font-size: 36px
            font-weight: bold
            color: white
            text-shadow: 3px 3px #242433
            margin-bottom: 25px
            &.start
                animation-name: fadein-effect
        .congratualtion
            color: red
            font-size: 36px
            font-weight: bold
            margin-bottom: 25px
        .lucky_user
            min-width: 30%
            opacity: 0
            animation-duration: .6s
            animation-fill-mode: forwards
            display: inline-block
            margin: 0 3% 25px 0
            font-size: 36px
            color: white
            text-shadow: 3px 3px #242433
            // border: 1px solid blue
            &.start
                animation-name: fadein-effect
            // &:nth-child(9n+2)
            //     // border: 1px solid red
            //     &:after
            //         content: ''
            //         // border: 1px solid red
            //         width: 100%
            //         margin: 35px
            //         display: block
            div
                display: inline-block
            .department
                width: 208px
                color: transparent
                position: relative
                .department_clip
                    position: absolute
                    top: 0
                    left: 0
                    width: 100%
                    text-align: justify
                    color: #CCC
                    &:after
                        content: ''
                        display: inline-block
                        width: 100%
                        height: 0
            .nickname
                color: #CCC
        .next_btn
            background-color: transparent
            border: 2px solid white
            border-radius: 10px
            color: white
            font-size: 28px
            font-weight: bold
            position: absolute
            bottom: -70px
            left: 0
            padding: 10px 20px
    .mask
        width: 100%
        height: 100%
        position: fixed
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        background-image: url('../images/sparkles.gif')
        // background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/13471/sparkles.gif')
        // background-image: url('../images/digital-city.gif')
        background-size: cover
        background-position: center center
        mix-blend-mode: color-dodge
        opacity: .2
        pointer-events: none
    .select_Bar
        // border: 1px solid red
        font-size: 36px
        position: absolute
        top: 70px
        right: 70px
        z-index: 11
        overflow: hidden
        select
            color: white
            background-color: transparent
            border: 1px solid white
            border-radius: 10px
            padding: 10px 20px
            margin-bottom: 10px
            float: right
        option
            // color: black
            background-color: rgba(255,255,255,0)
            position: relative
            font-size: 10px
            color: rgb(125,125,125)
            &:after
                content: ''
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                background-image: url('../images/shadow.png')
                background-size: 100% 100%
                border: 1px solid red
                
    .skip_btn
        font-size: 26px
        font-weight: bold
        color: yellow
        position: absolute
        bottom: 20px
        right: 20px
        cursor: pointer
    

@keyframes fadeout-effect
    0%
        opacity: 1
    100%
        opacity: 0
@keyframes fadein-effect
    0%
        opacity: 0
    100%
        opacity: 1

@media screen and ( max-width: 768px )
    .index
        height: 100vh
        .lottie_block
            width: 80vw
            height: 80vw
        .result
            width: 80vw
            height: 80vw