html, body 
    position: relative
    margin: 0 auto
    height: 100%
    max-width: 100vw
    max-height: 100vh
    overflow: hidden
    font-family: Arial,'Noto Sans TC','Microsoft JhengHei',Verdana,Arial,Helvetica,sans-serif,'Microsoft Yahei', 'Microsoft JhengHei','新細明體'
    background-image: url('../images/base-bg.png')
    background-position: top center
    background-size: cover
    // background: #b8ab99
a 
    cursor: pointer
    
.fb-root 
    height: 0
    overflow: hidden

._freeze 
    // overflow: hidden
    position: fixed
    width: 100%
    height: 100%

.l-container 
    margin: 0 auto
    position: relative


.l-wrap 
    width: 100%
    position: relative
    // overflow: hidden
    
.l-main
    // padding-bottom: $ft_height
    min-height: 100vh

.l-footer 
    position: relative
    text-align: center

.container-area
    width: 100%
    max-width: vw(1440)
    margin: 0 auto
.hidden
    display: none
.hide-text 
    overflow: hidden
    padding: 0
    text-indent: 101%
    white-space: nowrap

.loading_mask
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    z-index: 11
    background-color: #0087b4
    background-image: url("../images/cubes.png")
    &.active
        display: none